@import 'fonts';
@import 'variables';



/* *************** */
/* styles généraux */

html, body { height: 100% }

body {
  color: $gris;
  font-family: Raleway, sans-serif;
  font-size: 100%;
}

img {
  width: 100%;
}

.bouton {
  text-align: center;
  align-self: flex-end;
  font-family: "MADE Canvas", serif;
  font-size: 1.25em;
  -webkit-text-stroke-width: 0.025em;
  color: $blanc;
  background-color: $gris-fonce;
  padding: 0.5em 1em;
  margin-top: 1.5em;
  width: 200px;
  transition: 0.2s;
}

.bouton:hover {
  background-color: $gris;
}

/* *************** */



/* ************************* */
/* styles relatifs au header */

header {
  height: 100%;
  background: url("../img/bg_mobile.jpg") no-repeat top/cover;
}

.bandeNom {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4em;
  background-color: $gris-fonce;
  .nom {
    height: 2em;
  }
}

.accroche {
  display: flex;
  flex-direction: column;
  background-color: #fffd;
  backdrop-filter: blur(10px);
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 250px;
  clip-path: polygon(0 30%, 100% 0, 100% 100%, 0 100%);
  padding: 24px;
  .dev {
    align-self: flex-end;
    width: 50vmin;
    max-width: 280px;
    height: calc(50vmin / 2.66); // pour conserver le ratio du svg en fonction de sa largeur
    max-height: 107px;
    margin: 35px 0 16px 0;
  }
  span {
    font-size: 1.125em;
    line-height: 1.25;
    max-width: 520px;
  }
}

/* ************************* */



/* *********************** */
/* styles communs au corps */

.container {
  width: 100%;
  padding: 36px 24px;
}

.titre-section {
  font-family: "MADE Canvas", serif;
  font-weight: 900;
  font-size: 2em;
  text-align: center;
  -webkit-text-stroke-width: 0.025em;
}

/* *********************** */


/* ******************************** */
/* styles relatifs aux réalisations */

.real {
  margin: 36px 0;
}

.real_img {
  clip-path: polygon(3.4% 0, 100% 0, 79.5% 100%, 0 100%, 0 16%); // image biseautée
  shape-outside: polygon(3.4% 0, 100% 0, 79.5% 100%, 0 100%, 0 16%); // pour que le texte suive le biseau de l'image
  width: 45%;
  float: left;
  margin: 0 20px 20px 0;
}

.real_titre {
  position: relative;
  z-index: -1;
  padding: 8px 4%;
  line-height: 1;
  clip-path: polygon(58px 0, 100% 0, calc(100% - 8px) 100%, 50px 100%); // fond biseauté
  background-color: $or;
  color: $blanc;
  font-family: "MADE Canvas", serif;
  font-size: 1.25em;
  text-align: center;
  -webkit-text-stroke-width: 0.025em;
}

.real_desc {
  margin: 16px 0;
}

.real_lien::before {
  content: '> ';
}

.real_lien {
  display: block;
  text-align: right;
  text-transform: uppercase;
  text-decoration: underline;
  font-family: "MADE Canvas", serif;
  color: $gris;
  -webkit-text-stroke-width: 0.025em;
  margin-right: 24px;
  transition: 0.2s;
}

.real_lien:hover {
  color: $or;
  margin-right: 12px;
  cursor: pointer;
}

.clearfix {
  clear: both;
}

/* ******************************** */



/* **************************** */
/* styles relatifs aux parcours */

.section-parcours {
  background-color: $gris-clair;
}

.parcours {
  display: flex;
  flex-direction: column;
  ul {
    display: inline-block;
  }
}

.parc {
  margin: 18px 0;
  padding-left: 24px;
  border-left: 1px solid $gris;
  transition: 0.2s;
}

.parc:hover {
  transform: translateX(15px);
}

.parc_titre {
  font-family: "MADE Canvas", serif;
  font-size: 1.375em;
  font-weight: normal;
  -webkit-text-stroke-width: 0.025em;
  margin-bottom: 0;
}

.parc_date {
  font-family: "MADE Canvas", serif;
  -webkit-text-stroke-width: 0.025em;
  margin-bottom: 0;
}

.parc_lieu {
  font-weight: 200;
  margin-bottom: 0;
}

.parc_desc {
  margin-top: 0.5em;
}

.diplome {
  .parc_deco::before, .parc_deco::after {
    background-color: $or;
  }
  .parc_titre, .parc_desc {
    color: $or;
  }
}

.pro {
  .parc_deco::before, .parc_deco::after {
    background-color: $gris;
  }
  .parc_date {
    color: $or;
  }
}

/* **************************** */



/* ******************************* */
/* styles relatifs aux compétences */

.section-competences {

}

.bloc-competences {
  //margin-top: 2em;
  padding: 1.5em 1em 0 1em;
  border-top: 1px solid $or;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.bcomp_titre {
  font-family: "MADE Canvas", serif;
  -webkit-text-stroke-width: 0.025em;
  font-weight: normal;
  text-align: center;
  width: fit-content;
  position: relative;
  top: 0.75em;
  margin: 0 auto;
  padding: 0 0.5em;
  color: $or;
  background-color: $blanc;
}

.comp {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 8em;
  margin: 1rem 0 1.5rem 0;
  svg {
    width: 48px;
    height: 48px;
    filter: grayscale(1);
    margin-bottom: 2em;
    transition: 0.2s;
  }
  span {
    opacity: 0;
    position: absolute;
    transform: translateY(52px);
    transition: 0.2s;
    width: 8rem;
    text-align: center;
  }
}

.comp:hover, .comp:active {
  svg {
    filter: grayscale(0);
  }
  span {
    opacity: 1;
  }
}

/* ******************************* */



/* ******************************* */
/* styles relatifs à l'à-propos */

.section-a-propos {
  background-color: $gris-clair;
}

.a-propos {
  display: flex;
  flex-direction: column;
  align-items: center;
  .ap-photo {
    width: 75%;
    margin: 1rem 0;
  }
  .big {
    display: block;
    font-family: "MADE Canvas", serif;
    -webkit-text-stroke-width: 0.025em;
    font-size: 1.375rem;
    text-align: center;
    margin: 2rem 0 1rem 0;
  }
  .cint {
    display: flex;
    align-items: center;
    margin: 1rem 0;
    svg {
      width: 1.75rem;
      height: 1.75rem;
      fill: $or;
      margin-right: 1rem;
    }
  }
}

a.bouton {
  display: block;
  text-decoration: none;
  color: $blanc;
  margin: 1em auto;
  width: fit-content;
}

/* ******************************* */



/* ************************** */
/* styles relatifs au contact */

.formulaire {
  display: flex;
  flex-direction: column;

  .champ {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  label {
    font-family: "MADE Canvas", serif;
    -webkit-text-stroke-width: 0.025em;
    width: fit-content;
    margin: 0;
    padding: 0 0.5em;
    position: relative;
    top: 0.75em;
    left: 0.5em;
    background-color: $blanc;
    order: -1;
    transition: 0.2s;
  }

  input, textarea {
    appearance: none;
    border: 1px solid $gris;
    border-radius: 0;
    padding: 1em;
    transition: 0.2s;

    &:focus {
      outline: none;
      border-color: $or;

      & ~ label {
        color: $or;
      }
    }
  }

  .bouton.g-recaptcha {
    border: none;
  }

  .grecaptcha-badge {
    display: none !important;
  }

}

/* ************************** */



/* *************************** */
/* styles relatifs aux réseaux */

.section-reseaux {
  color: $blanc;
  background-color: $gris-fonce;
  padding: 24px;
  div {
    font-family: "MADE Canvas", serif;
    -webkit-text-stroke-width: 0.025em;
    font-size: 1.5em;
    text-align: center;
    margin: 18px auto;
  }
}

.reseaux {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  max-width: 400px;
}

.reseau {
  color: $blanc;
  transition: 0.2s;
  svg {
    width: 36px;
    height: 36px;
    fill: currentColor;
  }
}

.reseau:hover {
  color: $or;
}

/* *************************** */



/* ************************* */
/* styles relatifs au footer */

.section-footer {
  padding: .75rem;
  color: $blanc;
  background-color: $gris;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* ************************* */



/* ********************************* */
/* styles relatifs aux fiches projet */

.fiche-projet {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #333C;
  .fproj_fond {
    display: flex;
    flex-direction: column;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: white;
    overflow: hidden;
  }
  .fproj_container {
    padding: 24px;
    height: 100%;
    overflow: scroll;
  }
}

.fermer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  background-color: $gris-fonce;
  cursor: pointer;
  align-self: flex-end;
  flex-shrink: 0;
  top: 0; right: 0;
  z-index: 1;
  svg {
    width: 32px;
    height: 32px;
    stroke: $blanc;
  }
}

.fproj_entete {
  display: flex;
  flex-direction: column;
  margin: 0.25em 0 1em 0;
  .fproj_date {
    margin-bottom: 0.25em;
    font-family: "MADE Canvas", serif;
    -webkit-text-stroke-width: 0.025em;
  }
  .fproj_nom {
    display: flex;
    flex-direction: column;
    width: 100%;
    transform: translateX(-24px);
    padding: 8px 24px;
    clip-path: polygon(0 0, 100% 0, calc(100% - 8px) 100%, 0 100%); // fond biseauté
    background-color: $or;
    color: $blanc;
    font-family: "MADE Canvas", serif;
    font-size: 2em;
    -webkit-text-stroke-width: 0.025em;
    span:first-child {
      font-size: 0.5em;
    }
  }
}

.fproj_titre {
  margin: 1em 0 0.5em 0;
  font-family: "MADE Canvas", serif;
  font-size: 1.75em;
  font-weight: bold;
  text-align: center;
  -webkit-text-stroke-width: 0.025em;
}

.fproj_capture {
  display: flex;
  max-height: 400px;
  img {
    object-fit: contain;
  }

}

/* ********************************* */



/* ************************** */
/* styles relatifs aux toasts */

.toast-container {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 0 1rem 1rem 1rem;
  transition: 0.4s;
}

.toast-hidden {
  bottom: -60px;
  opacity: 0;
  z-index: -1;
}

.toast {
  width: 100%;
  max-width: 380px;
  text-align: center;
  margin: 0 auto;
  padding: 0.5rem 1.5rem;
  border-radius: 1rem;
  border: 1px solid transparent;
  box-shadow: 0 0 1rem 0 rgba($gris, 0.2);
}

.toast-success {
  color: #7acc7a;
  background-color: #e6ffe6;
  border-color: #7acc7a;
}

.toast-error {
  color: #cc7a7a;
  background-color: #ffe6e6;
  border-color: #cc7a7a;
}

/* ************************** */



@media screen and(min-width: 576px) {

  .container {
    width: 576px;
    margin: 0 auto;
  }

}

@media screen and (min-width: 768px), screen and (orientation: landscape) {

  header {
    background-image: url("../img/bg_tablet.jpg");
    background-position: top right;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .bandeNom {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }

  // l'accroche passe à gauche en mode paysage, peu importe la largeur
  .accroche {
    position: initial;
    width: 50vw;
    max-width: 600px;
    height: 350px;
    clip-path: polygon(0 20%, 100% 0, 100% 80%, 0 100%);
    .dev {
      margin-top: 60px;
    }
  }

}

@media screen and (min-width: 768px) {

  .container {
    width: 100%;
  }

  .titre-section {
    text-align: left;
  }

  .realisations {
    display: flex;
    max-height: 700px;
  }

  // disposition des réalisations en 60/40
  .realisations-gauche {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .realisations-droite {
    width: 40%;
  }

  .real {
    margin: 0;
    padding: 36px 18px;
  }

  // disposition des parcours à gauche ou à droite selon leur nature
  .parc {
    width: 50%;
    border: 0;
  }
  .diplome {
    text-align: right;
    align-self: flex-start;
    padding: 0 24px 0 0;
    border-right: 1px solid $gris;
  }
  .diplome:hover {
    transform: translateX(-15px);
  }
  .pro {
    align-self: flex-end;
    border-left: 1px solid $gris;
  }

  // disposition de la section à propos en ligne
  .a-propos {
    flex-direction: row-reverse;
    .ap-photo {
      width: 33%;
      margin-right: 2rem;
    }
    .ap-texte {
      width: 66%;
    }
    .big {
      text-align: left;
    }
  }

  // disposition des "petits" champs en ligne
  .ligne-champs {
    display: flex;
    .champ {
      width: 50%;
    }
    .champ:first-child {
      padding-right: 1.5em;
    }
  }

  .formulaire .bouton {
    align-self: center;
  }


  // les fiches projets ne prennent plus tout l'écran, elles "flottent" au 1er plan
  .fiche-projet .fproj_fond {
    width: 80%;
    max-width: 992px;
    height: 80%;
  }
  .fermer {
    top: 10%;
    right: 10%;
  }

  // la date passe à droite du nom du projet
  .fproj_entete {
    flex-direction: row;
    align-items: center;
    .fproj_nom {
      order: -1;
      width: 70%;
      float: left;
    }
    .fproj_date span {
      display: block;
      line-height: 1;
    }
  }

  .fproj_capture {
    max-height: 600px;
  }

  // la liste des langages affichée en 2 colonnes
  .fproj_langages {
    column-count: 2;
  }

}

@media screen and (min-width: 992px) {

  // container fixé en largeur et centré
  .container {
    width: 992px;
    margin: 0 auto;
  }

  // formulaire disposé à gauche
  .formulaire {
    width: 60%;
  }

  // section réseaux disposée à droite
  .section-reseaux {
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
    position: absolute;
    right: 0;
    width: 400px;
    height: 300px;
    margin: -200px 0; // section remontée pour paraître à côté du formulaire
    .biseau { // pour que le texte suive la forme de la section
      content: '';
      width: 25px;
      height: 150px;
      float: left;
      margin-left: 50px;
      shape-outside: polygon(0 0, 100px 0, 50px 100%, 0 100%);
    }
    div {
      padding: 0 0 0 50px;
      text-align: left;
      font-size: 2em;
    }
    .reseaux {
      padding: 0 50px;
    }
  }

  // section footer décalée pour être affichée sous les réseaux
  .section-footer {
    margin-top: 100px;
  }

  }

@media screen and (min-width: 1200px) {

  // grossissement global des polices sur grand écran
  body {
    font-size: 110%;
  }

  header {
    background-image: url("../img/bg_desktop.jpg");
    background-position: center;
  }

  .container {
    width: 1200px;
    margin: 0 auto;
  }

}

// titres des compétences apparents pour écrans tactiles
@media (hover: none) {
  .comp span {
    opacity: 1;
  }
}